.container.payResult {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #F7F7F7;
}
.container.payResult .content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 3.1rem 0.75rem 0.75rem 0.75rem;
}
.container.payResult .content .title {
  font-size: 0.9rem;
  color: #333333;
  margin-top: 1.3rem;
  font-weight: bold;
}
.container.payResult .content .text {
  font-size: 0.7rem;
  color: #B0B0B0;
  line-height: 1rem;
  margin: 0.7rem 1.5rem 0rem 1.5rem;
}
