.btn.btnRound {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  color: #AB61FF;
  width: 7.75rem;
  height: 2.1rem;
  border-radius: 1.05rem;
  margin: 0 auto;
  margin-top: 2.15rem;
  margin-bottom: 2.15rem;
  border: 1px solid #A678DA;
  opacity: 0.5;
}
.btn.btnRound.active {
  opacity: 1;
}
